//  (bootstrap)
.btn-custom-search {
    // button-variant Mixinsinclude   
    @include button-variant($primary, $primary); 
}


// mixin  
@mixin  customBtn($color) {
    border: 0;
    color: $color;
    border-radius: $border-radius-4;
    background-color: transparent;
    position: relative;
    transition: all .2s linear;
    .btn-inner {
        position: relative;
        z-index: 1;
    }

    &::before {
        content: "";
        width: 0%;
        height: 100%;
        display: block;
        border-radius: $border-radius-4;
        background-color: $color;
        position: absolute;
        top: 0;
        left: 0;
        transition: all .2s ease;
    }

    &:hover , :focus {
        border: 0;
        box-shadow: none;
        color: $white;
        &::before {
            width: 100%;
        }
    }
}

// 
.btn-moreBtn {
    @include customBtn($danger);
}

// 
.btn-infoBtn {
  @include customBtn($info);
}


