
//  - 
.home-activity{
    .card {
        cursor: pointer;
        border: 0;
        overflow: hidden;
    }

    .card-title {
        // 
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .card-body{
        padding: 0 1rem;
        height: 100%;
    }

    .imgWarp {
        display: block;
        overflow: hidden;
        border-radius: 12px;
    }

    .card-img {
        height: 80px;
        &:hover {
            transform: scale(1.2);
        }
    }
}

@include media-breakpoint-up(md) { 
    .home-activity{
        .card-body{
            border-radius: 0 12px 12px 0px;
            background-color: #F9F9F9;
            border: 1px solid #E5E5E5;
            //  border  border: unset  border: initial  border 
            border-left: unset; 
        }

        .imgWarp {
           border-radius: 0;
        }

        .card-img {
            border-radius: 12px 0 0 12px;
            height: 100%;
        }
    }
    
}



//  -  
.category-card{
    position: relative;
    .category-cardText {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        color: $white;
        font-weight: 700;
        opacity: .6;
    }

    &.active {
        box-shadow: 0 0 0 4px  $warning;
        .category-cardText   {
            opacity: 1;
         }

        .zoomImg {
                 transform: scale(1.2);
         }
    }
   
    &:hover {
        .category-cardText  {
            opacity: 1;
         }

         .zoomImg{
            transform: scale(1.2);
        }
    } 
}


//  - 
.resultList-card {
    cursor: pointer;

    .card-title-hover {
        color: $gray-black;
    }

   &:hover  { 
    .zoomImg { 
        transform: scale(1.2);
     }

     .card-title-hover {
         color: $primary;
     }
   }
}


//  - 
.nearbyInfo-card{
    .nearbyIcon {
        text-align: center;
        display: block;
    }

    .nearbyIcon-hover {
        text-align: center;
        display: none;
    }

    .nearbyInfo-cardtext{
        text-align: center;
    }


    &:hover{
        .nearbyIcon {
            display: none;
        }
    
        .nearbyIcon-hover {
            display: block;
        }

        .nearbyInfo-cardtext {
            color: $primary;
           
        }
    }

    &.active {
        box-shadow: $box-shadow;
        .nearbyIcon {
            display: none;
        }
    
        .nearbyIcon-hover {
            display: block;
        }

        .nearbyInfo-cardtext {
            color: $primary;
           
        }
    }
}

